<template>
    <div class="d-flex flex-column my-2">
            <small>
                <v-icon :color="getColor(item.full)" small> {{item.full ? 'mdi-file-check-outline' : 'mdi-file-remove-outline'}}</v-icon>
                Full Documents
            </small>
            <v-divider class="my-1"></v-divider>
            <v-layout d-flex>

            <small class="font-x-small">
               <v-icon :color="getColor(item.id1)" x-small>{{ geticon(item.sof) }}</v-icon> ID1
            </small>
             <small class="font-x-small">
               <v-icon :color="getColor(item.id2)" x-small>{{ geticon(item.sof) }}</v-icon> ID2
            </small>
             <small class="font-x-small">
               <v-icon :color="getColor(item.por)" x-small>{{ geticon(item.sof) }}</v-icon> POR
            </small>
            </v-layout>
          
            <small class="font-x-small">
               <v-icon :color="getColor(item.sof)" x-small>{{ geticon(item.sof) }}</v-icon> SOF
            </small>
    </div>
</template>
<script>
export default {
    name:'TableFieldUser',
    props: {
        item: {
            type: Object,
        },
        header: {
            type: Object
        }
    },
    methods: {
        getColor (val) {
            return val ? 'success': 'error'
        },
        geticon (val) {
            return val ? 'mdi-check': 'mdi-close'
        }
    }
}
</script>

<style>
.font-x-small {
    font-size: 9px;
    margin: 3px;
}
</style>